import mongoose from "mongoose";

export const assetModelSchema = new mongoose.Schema(
  {
    href: {
      type: String,
      require: true,
    },
    title: {
      type: String,
    },
    url: {
      type: String,
      required: true,
    },
    publicId: {
      type: String,
      required: true,
    },
    type: {
      type: String,
    },
    size: {
      type: Number,
    },
  },
  {
    _id: false,
  },
);
