import { StatusCodes } from "http-status-codes";

import { FooterDto } from "./dto/footer.dto";

export class FooterController {
  constructor(foooterService) {
    this.foooterService = foooterService;
  }

  async find(req, res) {
    const data = await this.foooterService.find();

    res.success({
      message: "Find Footer successfully!",
      status: StatusCodes.OK,
      data: data ? new FooterDto(data) : null,
    });
  }
}
