import { assetModelSchema } from "@/common/schema/model/asset-model.schema";
import { contactSchema } from "@/common/schema/model/contact-model.schema";
import mongoose, { Schema } from "mongoose";

const footerSchema = new Schema(
  {
    contact: contactSchema,
    location: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
    copyright: {
      type: String,
      required: true,
    },
    openingDay: {
      type: String,
      required: true,
    },
    openingHour: {
      type: String,
      required: true,
    },
    socialMedia: [assetModelSchema],
  },
  {
    collection: "footer",
    timestamps: true,
  },
);

const Footer = mongoose.model("Footer", footerSchema);

export default Footer;
