import { container } from "@/core/di/di-container";

import { FooterController } from "./footer.controller";
import { FooterRepository } from "./footer.repositoy";
import { FooterService } from "./footer.service";

export const footerDIKeys = {
  service: "footer-service",
  repository: "footer-repository",
  controller: "footer-controller",
};

container.register(footerDIKeys.repository, [], () => new FooterRepository());

container.register(
  footerDIKeys.service,
  [footerDIKeys.repository],
  (footerRepository) => new FooterService(footerRepository),
);

container.register(
  footerDIKeys.controller,
  [footerDIKeys.service],
  (footerService) => new FooterController(footerService),
);
