import { BaseResponse } from "@/core/response/base-response";
import { StatusCodes } from "http-status-codes";

export class FooterService {
  constructor(footerRepository, storageService) {
    this.footerRepository = footerRepository;
    this.storageService = storageService;
  }

  async create(footerDto, socialMediaIcons) {
    const data = await this.footerRepository.findAll();
    let uploadedsocialMediaIcons = [];

    if (data.length > 0) {
      throw BaseResponse.error({
        status: StatusCodes.CONFLICT,
        message: "Footer already exists !",
      });
    }

    if (socialMediaIcons && socialMediaIcons.length > 0) {
      uploadedsocialMediaIcons =
        await this.storageService.uploadFiles(socialMediaIcons);
    }

    footerDto.socialMedia = uploadedsocialMediaIcons;

    return this.footerRepository.createFooter(footerDto);
  }

  async find() {
    return this.footerRepository.findAll()[0];
  }

  async update(footerDto, files) {
    const data = await this.footerRepository.find()[0];

    if (!data) {
      throw BaseResponse.error({
        status: StatusCodes.NOT_FOUND,
        message: "Footer not found !",
      });
    }

    return data;
  }
}
