import { container } from "@/core/di/di-container";
import express from "express";

import { footerDIKeys } from "./footer.di";

const prefix = "/footers";

const footerRouter = express.Router();

const footerController = container.get(footerDIKeys.controller);

footerRouter.get(`${prefix}`, (req, res) => footerController.find(req, res));

export default footerRouter;
